import {Component} from "react";
import {Container,Row,Col} from "reactstrap";
import "../index.css";

class Responsability extends Component{
    render(){
        return(
            <div className="c2" style={{padding:"30px 0"}}>
                <Container>
                    <Row>
                        <Col>
                        <h1 style={{fontFamily:"Concert One",textAlign:"center"}}>Nuestras responsabilidades con la sociedad </h1>
                    <ul style={{fontWeight:"550",fontSize:"17px"}}>
                        <li>
                        Rendición de cuentas:
En nuestro micro emprendimiento los socios responden a las órdenes o sugerencias de los administradores.
                        </li>
                        <li>
                        Transparencia:
Gracias a nuestra página web y a nuestra página de instagram, los posibles consumidores pueden ver todo lo que se hace dentro de nuestro micro emprendimiento, como se produce, nuestras ofertas y promociones, los productos terminados, quienes son los integrantes del grupo de trabajo,etc.

                        </li>
                        <li>
                         Comportamiento ético:
"Cri-cri sahumerios" es una empresa que le interesan los gustos o las preferencias de sus clientes, por lo tanto utilizamos materiales que en su medida no dañinos para el planeta ni para la salud de los consumidores.

                        </li>
                        <li>
                         Respeto a la ley:
Nuestra empresa tiene firmados contratos de locación, de mutuo y de sociedad colectiva,entre otros documentos legales que permiten su funcionamiento acorde a las normas establecidas por la ley. 

                        </li>
                        <li>
                            
 Respeto a los derechos humanos.
"Cri-cri sahumerios" es una empresa en donde todos los socios pueden opinar o trabajar de igual manera, sin importar raza, sexo ni religión. 
                        </li>
                    </ul>
                        </Col>
                    </Row>
                </Container>

            </div>
        );
    };
};

export default Responsability;