import { Component } from "react";
import {Container,Row,Col,CarouselCaption,CarouselItem,CarouselControl,CarouselIndicators,Carousel} from "reactstrap";
import "../index.css";


const items = [
	{
		src: 'https://i.ibb.co/7zDM5xw/Whats-App-Image-2021-11-08-at-12-40-51-PM.jpg"',
		altText: '',
		caption: ''
	},
	{
		src: 'https://i.ibb.co/SR8d4St/Whats-App-Image-2021-11-08-at-12-40-50-PM.jpg',
		altText: '',
		caption: ''
	},
	{
		src: 'https://i.ibb.co/T0pVr24/Whats-App-Image-2021-11-08-at-12-40-49-PM.jpg',
		altText: '',
		caption: ''
	},
	{
		src: 'https://i.ibb.co/j5GDnFd/Whats-App-Image-2021-11-11-at-8-58-57-AM.jpg',
		altText: '',
		caption: ''
	},
    {
		
		src: 'https://i.ibb.co/J5fvf6n/Whats-App-Image-2021-11-03-at-9-25-52-PM.jpg',
		altText: '',
		caption: ''
	}
    
];



class ProductionCarousel extends Component {
	constructor(props) {
		super(props);
		this.state = { activeIndex: 0 };
		this.next = this.next.bind(this);
		this.previous = this.previous.bind(this);
		this.goToIndex = this.goToIndex.bind(this);
		this.onExiting = this.onExiting.bind(this);
		this.onExited = this.onExited.bind(this);
	}

	onExiting() {
		this.animating = true;
	}

	onExited() {
		this.animating = false;
	}

	next() {
		if (this.animating) return;
		const nextIndex = this.state.activeIndex === items.length - 1 ? 0 : this.state.activeIndex + 1;
		this.setState({ activeIndex: nextIndex });
	}

	previous() {
		if (this.animating) return;
		const nextIndex = this.state.activeIndex === 0 ? items.length - 1 : this.state.activeIndex - 1;
		this.setState({ activeIndex: nextIndex });
	}

	goToIndex(newIndex) {
		if (this.animating) return;
		this.setState({ activeIndex: newIndex });
	}

	render() {
		const { activeIndex } = this.state;

		const slides = items.map((item) => {
			return (
				<CarouselItem
					onExiting={this.onExiting}
					onExited={this.onExited}
					key={item.src}
				>
					<img src={item.src} alt={item.altText} className="carousel-img" />
					<CarouselCaption captionText={item.caption} captionHeader={item.caption} />
				</CarouselItem>
			);
		});

		return (
			<div className="c3" style={{paddingTop:"20px",paddingBottom:"60px"}}>
               <Container>
                <Row>
                    <Col xs="12" md="9" style={{margin:"auto"}}>
                <h1 style={{fontFamily:"Concert One",marginBottom:"20px",textAlign:"center",color:"#c5d7bd"}}>Como trabajamos?</h1>
				<Carousel 
					activeIndex={activeIndex}
					next={this.next}
					previous={this.previous}
				>
					{slides}
					<CarouselControl direction='prev' directionText='.' onClickHandler={this.previous} />
					<CarouselControl direction='next' directionText='.' onClickHandler={this.next} />
				</Carousel>
                <h1 style={{fontFamily:"Concert One",margin:"10px 0",textAlign:"center"}}></h1>

                    </Col>
                </Row>
               </Container>
			</div>
		);
	}
}

export default ProductionCarousel;