import {Component} from "react";
import {Container,Row,Col} from "reactstrap";
import "../index.css";

class Ecology extends Component{
    render(){
        return(
            <div style={{padding:"50px 0",backgroundColor:"white"}}>
                <Container>
                    <Row>
                        <Col xs="12">
                            <h1 style={{fontFamily:"Concert One",textAlign:"center"}}>Nuestra responsabilidad con el ambiente</h1>
                            <h3 style={{fontFamily:"Concert One"}}>Somos una empresa ecológicamente responsable</h3>
                            <p style={{fontWeight:"500",fontSize:"17px"}}>
                            ¿Por qué? Porque consideramos todos los impactos que generan la producción y operación de nuestra empresa.
                             Somos responsables de asegurar a generaciones futuras su crecimiento. 
                             ¿Cómo hacemos esto? Nuestros productos han sido evaluados,
                              junto a la materia prima que utilizamos en su elaboracion para asegurarnos 
                              de que los mismos no tengan efectos contaminantes hacia los consumidores y la capa de ozono. 
                            </p>
                        </Col>
                    </Row>
                </Container>

            </div>
        );
    };
};

export default Ecology;