import {Component} from "react";
import {Container,Row,Col} from "reactstrap";
import "../index.css";

class Recomendations extends Component{
    render(){
        return(
            <div className="c1" style={{paddingBottom:"40px"}}>
                <Container>
                    <Row>
                        <Col xs="12">
                    <h1 style={{textAlign:"center",fontFamily:"Concert One",paddingBottom:"10px",paddingTop:"45px"}}>Consejos para utilizar nuestros sahumerios...</h1>
                        
                        </Col>
                    </Row>     
                    <Row>
                    <Col xs="12" md="6">
                        <ul style={{listStyle:"none"}}>
                            <li style={{fontFamily:"Helvetica",fontWeight:"550",padding:"8px 0"}}>


                           ●	Si lo que procuras es cambiar o renovar un aroma en particular, un sahumerio podría ser de gran ayuda para perfumar el ambiente con diferentes fragancias.
                            </li>
                            <li  style={{fontFamily:"Helvetica",fontWeight:"550",padding:"8px 0"}}>
                            ●  Para algunos, un sahumerio se ha hecho un ritual y sirve de gran ayuda para descargar y desconcectar por un cierto momento.
                           
                            </li>
                        </ul>
                        </Col>  
                    <Col xs="12" md="6">
                    <ul style={{listStyle:"none"}}>
                            <li style={{fontFamily:"Helvetica",fontWeight:"550",padding:"8px 0"}}>
                            ●   Se cree que los sahumerios también renuevan energías y sacan las malas vibras que uno absorve del día a día. Prender un sahumerio y moverlo con movimiento circulares hacia la derecha quitan esas energías y suman o aportan buenas sensaciones. 
                            </li>
                            <li style={{fontFamily:"Helvetica",fontWeight:"550",padding:"8px 0"}}>
                            ●   También una sanitización propia o interna, es prender uno y agitarlo desde la cabeza hacia los pies. 

                            </li>
                            
                        </ul>
                        </Col>  

                    </Row>               
                </Container>
            </div>
        );
    };
};

export default Recomendations